"use client";


import { useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';

export default function Home() {
  const [file, setFile] = useState(null);
  const [vehicleNumber, setVehicleNumber] = useState('');
  const [area, setArea] = useState('');

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log({
      file: file ? file.name : 'No file uploaded',
      vehicleNumber,
      area,
    });
    // Reset form
    setFile(null);
    setVehicleNumber('');
    setArea('');
  };

  return (
    <div className="min-h-screen bg-gray-100">
      {/* Navigation Bar */}
      <nav className="bg-gray-500 p-4 flex justify-between items-center">
        <div className="space-x-8">
          <Link href="/" className="text-white hover:text-gray-200">
            Home
          </Link>
          <Link href="/activity" className="text-white hover:text-gray-200">
            Activity
          </Link>
          <Link href="/about" className="text-white hover:text-gray-200">
            About
          </Link>
        </div>
        <div>
          <Image src="/user-icon.png" alt="User" width={24} height={24} />
        </div>
      </nav>

      {/* Main Content */}
      <main className="p-8 flex justify-center">
        <div className="flex space-x-8 max-w-4xl w-full">
          {/* Left: Upload Section */}
          <div className="bg-gray-400 w-1/2 h-48 flex items-center justify-center rounded-lg">
            <label htmlFor="file-upload" className="cursor-pointer">
              <span className="text-gray-700">Upload image or video</span>
              <input
                id="file-upload"
                type="file"
                accept="image/*,video/*"
                className="hidden"
                onChange={handleFileChange}
              />
            </label>
          </div>

          {/* Right: Form Section */}
          <div className="bg-gray-300 w-1/2 p-6 rounded-lg">
            <form onSubmit={handleSubmit}>
              <div className="mb-4">
                <label htmlFor="vehicle-number" className="block text-gray-800 font-medium mb-1">
                  Vehicle Number
                </label>
                <input
                  id="vehicle-number"
                  type="text"
                  value={vehicleNumber}
                  onChange={(e) => setVehicleNumber(e.target.value)}
                  className="w-full p-2 border border-gray-400 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>
              <div className="mb-4">
                <label htmlFor="area" className="block text-gray-800 font-medium mb-1">
                  Area
                </label>
                <div className="relative">
                  <select
                    id="area"
                    value={area}
                    onChange={(e) => setArea(e.target.value)}
                    className="w-full p-2 border border-gray-400 rounded appearance-none focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  >
                    <option value="">Select an area</option>
                    <option value="Area 1">Area 1</option>
                    <option value="Area 2">Area 2</option>
                    <option value="Area 3">Area 3</option>
                  </select>
                  <div className="absolute right-3 top-1/2 transform -translate-y-1/2 pointer-events-none">
                    <Image src="/search-icon.png" alt="Search" width={16} height={16} />
                  </div>
                </div>
              </div>
              <button
                type="submit"
                className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700 transition"
              >
                Submit
              </button>
            </form>
          </div>
        </div>
      </main>
    </div>
  );
}